const { createApp } = require('vue')
const VendorPortal = require('../views/VendorPortal.vue').default
const { sharedPinia } = require('../store/shared')
require('../assets/styles.css')

// Extract token from URL path
function extractTokenFromPath() {
  console.log('Current URL:', window.location.href)
  console.log('Current pathname:', window.location.pathname)
  
  const pathParts = window.location.pathname.split('/')
  console.log('Path parts:', pathParts)
  
  const tokenIndex = pathParts.findIndex(part => part === 'token')
  console.log('Token index:', tokenIndex)
  
  if (tokenIndex !== -1 && pathParts[tokenIndex + 1]) {
    const token = pathParts[tokenIndex + 1]
    console.log('Extracted token:', token)
    return token
  }
  
  // Fallback: try to get from query params
  const urlParams = new URLSearchParams(window.location.search)
  const queryToken = urlParams.get('token')
  console.log('Query token:', queryToken)
  return queryToken
}

const app = createApp(VendorPortal, {
  props: {
    token: extractTokenFromPath()
  }
})

app.use(sharedPinia)

app.mount('#app')
